.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

@keyframes realtime-text-color-pulse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
.no-overflow {
  word-break: break-word;
  word-wrap: break-word;
}

.disabled {
  opacity: 0.4;
  pointer-events: none;
}

.no-wrap {
  display: inline-block;
  text-decoration: inherit;
  white-space: nowrap;
}

.inline-block {
  display: inline-block;
}

.strikethrough {
  color: #494f5c;
  text-decoration: line-through;
}

.callout {
  background-color: #f2f3f5;
  border: 1px solid #e9eaed;
  border-radius: 0.25rem;
  margin: 0.75rem 0 1rem;
  padding: 0.75rem;
}
.callout:empty {
  display: none;
}

.callout-small {
  padding-bottom: 0.5rem;
  padding-top: 0.5rem;
}
.callout-small .icon-bus,
.callout-small .icon-subway,
.callout-small .icon-logan-express,
.callout-small .icon-massport-shuttle {
  height: 1.25em;
  margin-right: 0.25em;
  position: relative;
  top: 0.125em;
  width: 1.25em;
}

.callout-full-height {
  margin: 0;
}

.callout-heading {
  margin-top: 0;
}

.has-element-overlay:hover .element-overlay {
  display: block;
}

.element-overlay {
  background-color: rgba(28, 30, 35, 0.8);
  bottom: 0;
  display: none;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
}

.element-overlay-icon {
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}

.sticky-top {
  position: sticky;
  top: 0;
  z-index: 13;
}

.stripe-bar {
  background: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAYAAAAGCAYAAADgzO9IAAAAAXNSR0IArs4c6QAAABZJREFUCB1jYMAC7t279x9DmG6CQJsBUrkVl6gmw/kAAAAASUVORK5CYII=") repeat;
